<script>
import { forceRerender } from "@/components/Misc/forceRerender";
import axios from "axios";

export default {
  name: "FooterApp",
  mixins: [forceRerender],
  data() {
    return {
      email: null,
      loading: false,
    }
  },
  methods: {
    saveEmail() {
      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        this.loading = true;
        const email = this.email;
        this.email = null;
        this.forceRerender();
        axios.post(this.$store.state.host + '/website/api/v1/website/create-newsletter', {
          email,
        }).then(() => {
          this.notify('ایمیل شما در خبرنامه ثبت شد', 'info');
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.loading = false;
        })
      }
    }
  }
}
</script>

